<template>
  <b-container class="h-100">
    <div class="row h-100">
      <div class="col my-auto">
        <el-card shadow="always" class="mx-auto text-center"
          ><h1>Page not found</h1>
          <el-button type="primary" @click="$router.push('/')">{{
            $t("Back to HOME")
          }}</el-button>
        </el-card>
      </div>
    </div>
  </b-container>
</template>

<script>
export default {
  name: "not-found",
  props: ["bgColor"],
};
</script>

<style scoped>
body {
  background-image: url("/static/bg-human.jpg");
}
</style>


